@use '../1-helpers/variables' as v;
@use '../1-helpers/mixins' as m;

html {
  max-height:100%;
  position:relative;
  transition-property: background-color, color;
}

body {
  //TODO
  /*margin: 10vw;*/
  &:before {
    position: fixed;
    z-index: -1;
  }
}

#content-wrapper {
  will-change: filter;
  transition: filter .3s ease-in-out;
}

#bg-coa{
  opacity: 0.15;
  position: fixed;
  right: 0;
  margin: 10vh;
  @include m.fullImage();
  max-height: 10vh;
  mix-blend-mode: multiply;
}

#main-grid {
  max-height:100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  max-width: v.$bpTablet;
  margin-left: v.$mb-15;
  margin-right: v.$mb-15;
}

.page-section{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.page-section__text>p {
  padding: 1rem 0;
}

.grid {
  display: grid;
  gap: v.$mb-15;
}

.fade-in {
  box-sizing: border-box;
  opacity: 0;
  transition: all .6s ease-in-out;
  transform: translate(0, 10%) scale(.99);
}

.fade-in__left {
  box-sizing: border-box;
  opacity: 0;
  transition: all .6s ease-in-out;
  transform: translate(-10%, 0) scale(.99);
}

.fade-in__right {
  box-sizing: border-box;
  opacity: 0;
  transition: all .6s ease-in-out;
  transform: translate(10%, 0) scale(.99);
}

.fade-in.appear {
  opacity: 1;
  transform: translate(0, 0) scale(1);
}

.info-illustration {
  /*margin-top: 2vh;*/
  min-width: 100%;
  max-height: 30vh;
  margin: 5vh 0;
}

@media screen and (min-width: v.$bpLaptop),
screen and (max-height: 500px) and (min-width: v.$bpMobile) {
  .page-section{
    flex-direction: row;
    justify-content: space-between;
    flex-grow: 100;
    &:nth-of-type(even){
      flex-direction: row-reverse;
    }
  }

  .page-section__text {
    max-width: 100%;
  }

  .info-illustration {
    min-width: 40%;
    margin-right: 4vw;
    max-height: 50vh;
  }
}

@media (min-width: 40em) {
  .split {
    display: flex;
    gap: 2em;
  }
}
section {
  display: flex;
  align-items: center;
}

.section-title {
  padding-bottom: 1rem;
  text-align: center;
}

@media screen and (min-width: v.$bpLaptop){
  .section-title {
    text-align: left;
  }
}

section.subtitle {
  display: block;
  font-size: small;
}
section.page-section {
  padding: 10vh v.$pdd-side;
  min-height: 80vh;
  max-height:100%;
}

section.page-section:nth-of-type(even) {
  background: v.$clr-primary;
  box-shadow: inset 0 -7px 9px rgba(0 0 0 /.4);
}

section.page-section:last-of-type {
  background: transparent;
  box-shadow: none;
}

/* ==== HOME SECTION==== */
section.slider{
  min-height: 95vh;
  //noinspection CssUnknownTarget
  //background-image: linear-gradient(245deg, v.$clr-third 0%, v.$clr-bg 80%), url(data-url:../media/Background-fluid-lines-anim.svg);
  background-size: cover;
  background: linear-gradient(245deg, v.$clr-third 0%, v.$clr-bg 80%) no-repeat 0 0;
}

#background-anim {
  position: fixed;
  left: 0;
  top: 0;
  min-width: 100vw;
  min-height: 100vh;
  /*bottom: 0;
  right: auto;*/
  object-fit: cover;
  object-position: 0;
  z-index: -1;
  opacity: .7;
}

.home__container  {
  margin: 0 v.$pdd-side;
  gap: 1rem;
}

.home__content {
  grid-template-columns: .7fr 3fr;
  padding-top: 5vh;
  padding-bottom: v.$pdd-tb;
  align-items: center;
}

@media screen and (min-width: v.$bpTablet),
screen and (max-height: 500px) and (min-width: v.$bpMobile) {
  #home {
    position: relative;
    min-height: clamp(720px, 100vh, 2000px);
  }
  .home__content {
    grid-template-columns: .7fr 3fr;
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    padding-top: 0;
    padding-bottom: 5vh;
  }


}

.home__socials {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
}

.home__social-icon {
  font-size: 1.5em;
  color: v.$clr-primary;
  &:hover {
    color: v.$clr-text;
    transform: scale(1.2);
  }
}

.home__image {
  width: clamp(50px, 50px + 25vw, 285px);
  fill: v.$clr-primary;
}

.home__data {
  grid-column: 1/3;
}

.scroll-down {
  margin-top: 1rem;
  display: inline-block;
}

/* ==== PORTFOLIO SECTION ==== */
#portfolio {
  min-height: 100vh;
}
.portfolio__head {
  /*display: flex;*/
  @media screen and (min-width: v.$bpLaptop) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.portfolio__button-more{
  display: none;
  align-content: center;
  @media screen and (min-width: v.$bpLaptop) {
    display: inline;
  }
  padding: 0;
  margin: 10px 10px 20px 10px;
  align-items: center;
}

/* ==== ABOUT SECTION ==== */
.about__button {
  margin-top: 4rem;
}

.about__experiences {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: 2rem 0;
  & > * {
    flex-basis: max-content;
  }
}

@media screen and (min-width: v.$bpMobile){
  .about__experiences {
    flex-flow: row wrap;
  }
}

.about__experience {
  display: flex;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 1rem;
  margin: 1vw;
  background-color: v.$clr-bg;
  border-radius: v.$radius;
  transition: (all .3s ease-in-out);
  &:hover {
    transform: scale(1.1);
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
  }
  line-height: 1.5rem;
}

.about__experience-years {
  font-weight: bold;
  font-size: 1.5em;
  padding-bottom: .8rem;
}

/* ==== CONTACT SECTION ==== */
.contact {
  padding: 1rem;
  justify-content: center;
}

.contact-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  --border: 3.5px;
  padding: 3rem;
  width: 100%;
  min-width: 250px;
  height: 100%;
  border-radius: v.$radius;
  /*border: 0.15em solid v.$clr-text;*/
  background: v.$clr-bg;

  &::before {
    position: absolute;
    content: "";
    top: calc(-1 * var(--border));
    left: calc(-1 * var(--border));
    z-index: -1;
    width: calc(100% + var(--border) * 2);
    height: calc(100% + var(--border) * 2);
    background: linear-gradient(
                    60deg,
                    #2C4A81,
                    v.$clr-primary,
                    #488fc7,
                    v.$clr-primary,
                    #22c0fa,
                    #274377
    );
    background-size: 300% 300%;
    border-radius: v.$radius * 1.2;
    animation: moveGradient 7s alternate infinite;
    @include m.shadow;
  }
}

@media screen and (min-width: v.$bpLaptop) {
  .contact-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
  }

}


/*---------------------------------------------*/
textarea, input {
  outline: none!important;
  border: none;
  cursor: text;
  &:focus {
    border-color: transparent !important;
  }
}

input.input {
  height: 45px;
}

textarea.input {
  min-height: 115px;
  padding-top: 13px;
  padding-bottom: 13px;
}

#contact-form {
  padding-top: 1rem;
  .input-wrapper {
    width: 100%;
    position: relative;
    border-bottom: 2px solid v.$clr-third;
    margin-bottom: 37px;
  }
  textarea {
    resize: none;
  }
  .contact-form__input {
    background-color: transparent;
  }

  #contact-form__submit-message{
    color: v.$clr-primary;
    font-weight: normal;
    margin-bottom: 3vh;
    @include m.smoothTransition(.3s);
  }
  .input {
    display: block;
    width: 100%;
    font-size: 15px;
    color: v.$clr-text;
    line-height: 1.2;
  }
  .input-placeholder{
    color: v.$clr-text;
    position: absolute;
    top: 15px;
    left: 0;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    pointer-events: none;
  }

  //slide label up on focus or input
  input:focus ~ label,
  textarea:focus ~ label,
  input:valid ~ label,
  textarea:valid ~ label {
    font-size: 0.75em;
    color: v.$clr-secondary;
    top: -15px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
  }

  //sub line
  .input-border {
    position: absolute;
    left: 0;
    bottom: -2px;
    width: 0;
    height: 2px;
    background: linear-gradient(
                    60deg,
                    v.$clr-primary,
                    v.$clr-primary
    );
    @include m.smoothTransition(.3s)
  }
  input:focus ~ .input-border,
  input:valid ~ .input-border,
  textarea:focus ~ .input-border,
  textarea:valid ~ .input-border{
    width: 100%;
  }
  .input-valid {
    width: 100%;
    background: linear-gradient(
                    60deg,
                    #274377,
                    #22c0fa,
                    #274377
    );
  }
  .contact-form__submit {
    margin-top: 0;
  }
}

input:focus::-webkit-input-placeholder { color:transparent; }
input:focus:-moz-placeholder { color:transparent; }
input:focus::-moz-placeholder { color:transparent; }
input:focus:-ms-input-placeholder { color:transparent; }

textarea:focus::-webkit-input-placeholder { color:transparent; }
textarea:focus:-moz-placeholder { color:transparent; }
textarea:focus::-moz-placeholder { color:transparent; }
textarea:focus:-ms-input-placeholder { color:transparent; }

input::-webkit-input-placeholder { color: #999999; }
input:-moz-placeholder { color: #999999; }
input::-moz-placeholder { color: #999999; }
input:-ms-input-placeholder { color: #999999; }

textarea::-webkit-input-placeholder { color: #999999; }
textarea:-moz-placeholder { color: #999999; }
textarea::-moz-placeholder { color: #999999; }
textarea:-ms-input-placeholder { color: #999999; }



