@mixin fullImage{
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 0;
}

@mixin flex{
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin noOverflow{
  box-sizing: border-box;
  max-height:100%;
  max-width:100%;
}

@mixin shadow{
  box-shadow: 0 4px 9px rgba(0 0 0 /.4);
}

@mixin transition($selector){
  transition: $selector .3s ease-in-out;
}

@mixin smoothTransition($time){
  transition: all $time cubic-bezier(0.88, 0.35, 0.27, 1);
}

@mixin speedUpTransition($time){
  transition: all $time cubic-bezier(1, 0, 1, 0);
}

@mixin stroke-anim{
  stroke-dasharray: 90;
  stroke-dashoffset: 80;
  animation: line-anim 5s infinite ease-in-out;
}